@font-face {
  font-family: 'Open Sans';
  font-weight: 400;
  src: local('Open Sans'), url(./fonts/OpenSans-Regular.ttf) format('truetype');
}
@font-face {
  font-family: 'Open Sans';
  font-weight: 700;
  src: local('Open Sans'), url(./fonts/OpenSans-Bold.ttf) format('truetype');
}
.App{
  margin-left: 300px;
  margin-right: 300px;
  margin-top: 40px;
  font-family: 'Open Sans';
}
.menu{
  position: absolute;
  top:30px;
  font-size: 20px;
  right: 600px;
  display: flex;
  justify-content: space-between;
  width: 200px;
}
.menuWrapper{
  //margin-top: 40px;
  //margin-right: 100px;
  //margin-left: 1000px;
}
.headerWrapper{
  display: flex;
  margin-top: 26px;
  justify-content: space-between;
  width: 100%;
}
.header{
  z-index: 100;
  height: 80px;
  background: white;
  position: fixed;
  top:0;
  width: 100%;
}
.footer{
  background: #000000;
  width: 100%;
  height: 560px;
}
.footerText{
  color: white;
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: bold;
  font-size: 44px;
  line-height: 55px;
  width: 630px;
  height: max-content;
}
.footerWrapper{
  padding-top: 4%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-left: 300px;
  margin-right: 300px;
}
.footerMail{
  width: 630px;
  height: 200px;
}
.active{
  border-bottom: 1px solid black;
}
NavLink{
  color: black;
  text-decoration: none;
  font-size: 16px;
}
a{
  color: black;
  text-decoration: none;
}
.hrefToPerson{
  padding-bottom: 1px;
  border-bottom: 1px solid black;
}
.arrowTop{
  cursor: pointer;
  position: fixed;
  right: 60px;
  bottom: 100px;
  opacity: 0;
  transition: opacity ease-in-out .3s;
}
.contact-form{
  display: flex;
  flex-direction: column;
}
.topBarEmail{
  width: 85.6%;
  display: flex;
  justify-content: space-between;
}
.blockEmail{
  display: flex;
  flex-direction: column;
}
label{
  font-size: 14px;
  color: #a5a5a5;
  padding-bottom: 7px;
}
.inputEmail{
  margin-bottom: 30px;
  width: 130%;
  height: 34px;
  border-style: solid;
  border-width: 2px;
  border-color: hsla(0, 0%, 100%, 0.02);
  border-radius: 0px;
  background-color: #161616;
  font-family: 'Open Sans';
  line-height: 1.42857143;
  padding-left: 12px;
  padding-right: 12px;
  padding-bottom: 8px;
  padding-top: 8px;
  color: #fff;
  outline: none;
  font-size: 16px;
  font-weight: 400;
}
::placeholder{
  color: hsla(0, 0%, 100%, 0.25);;
}
.message{
  padding-left: 12px;
  padding-right: 12px;
  padding-bottom: 8px;
  padding-top: 8px;
  margin-bottom: 30px;
  height: 80px;
  font-family: "Open Sans";
  border-style: solid;
  border-width: 2px;
  border-color: hsla(0, 0%, 100%, 0.02);
  background-color: #161616;
  line-height: 1.42857143;
  color: #fff;
  outline: none;
  font-size: 16px;
  font-weight: 400;
  resize: none;
}
.sendEmail{
  padding-left: 22px;
  padding-right: 22px;
  font-family: "Open Sans";
  font-weight: bold;
  font-size: 16px;
  text-align: center;
  vertical-align: center;
  color: black;
  background: white;
  border-radius: 30px;
  width: max-content;
  height: 44px;
  cursor: pointer;
  outline: none;
  border: transparent;
}
.activeSend{
  display: none;
}
.afterSend{
  display: none;
  position: absolute;
  top:0;
  border-style: solid;
  border-width: 2px;
  border-color: hsla(0, 0%, 100%, 0.02);
  background-color: #161616;
  height: 310px;
  width: 100%;
  color: white;
  font-weight: normal;
  font-size: 16px;
  text-align: center;
}
.emailWrapper{
  position: relative;
}
.afterSendText{
  margin-top: 8%;
}
.chek{
  margin-top: 8%;
}
.topStText{
  margin-bottom: 3px;
}
.botFooter{
  align-items: center;
  width: 68%;
  display: flex;
  justify-content: space-between;
  color: grey;
  margin-top: 200px;
  margin-left: 300px;
  margin-right: 300px;
}
.icons{
  //width: max-content;
  height: max-content;
  display: flex;
  align-items: flex-end;
}
.iconTelegram{
  fill:grey;
  transition: fill ease-in-out .25s;
}
.telegram{
  padding-left: 25px;
}
.telegram:hover{
  cursor: pointer;
  .iconTelegram{
    fill:white;
  }
}
.topBarProjects{
  color: black;
  width: 100%;
  display: flex;
  margin-top: -30px;
  justify-content: space-between;
  height: max-content;
}
.next{
  position: absolute;
  left: 1300px;
  top:150px;
  font-size: 20px;
  color: #9c9c9c;
  width: 300px;
  display: flex;justify-content: space-between;
  align-items: center;
}
